<template>
  <div class="editor">
    <FileDrop @drop='onDrop'></FileDrop>
    <el-dialog
      title="Result"
      width="50%"
      :visible.sync="dialogVisible"
    >
      <el-button class="btn" type="success" plain @click="copy" data-clipboard-target="#result">Copy</el-button>
      <el-input
        type="textarea"
        autosize
        id="result"
        v-model="result_text">
      </el-input>
    </el-dialog>

    <el-dialog
      title="Paste"
      width="50%"
      :visible.sync="readDialogVisible"
    >
      <el-button class="btn" type="success" plain @click="paste" data-clipboard-target="#result">Apply</el-button>
      <el-input
        type="textarea"
        id="paste"
        :rows="5"
        v-model="paste_text">
      </el-input>
    </el-dialog>

    <el-container>
        <el-main>
          <el-row :gutter="20">
            <el-col :span="12"><el-button class="btn" plain @click="read">Read</el-button></el-col>
            <el-col :span="12"><el-button class="btn" type="success" plain @click="write">Done</el-button></el-col>
          </el-row>
          <component ref="component" :is="model.component" v-bind="model.props || {}"></component>
        </el-main>
    </el-container>
  </div>
</template>

<script>
let toml = require('@iarna/toml');
let ClipboardJS = require('clipboard')

export default {
  name: 'Editor',
  props: ['model'],
  data: function() {
    return {
      dialogVisible: false,
      readDialogVisible: false,
      result_text: "",
      paste_text: ""
    }
  },
  methods: {
    read() {
      this.readDialogVisible = true
    },
    paste() {
      this.tryApply(this.paste_text)
    },
    tryApply(d) {
      try {
        let json = toml.parse(d)
        console.log(json)
        this.$refs['component'].read(json)
        this.$message({
            message: 'OK',
            type: 'success'
        });
      } catch(e) {
        this.$message({
          message: 'Failed to read file',
          type: 'error'
        });
      }
    },
    write() {
      new ClipboardJS('.btn');
      let json = this.$refs['component'].write()
      console.log(json)
      let render = toml.stringify(json)
      this.result_text = render
      this.dialogVisible = true
    },
    onDrop(formData, files) {
      let file = files[0];
      let reader = new FileReader()
      let d = reader.readAsText(file)

      this.tryApply(d)
    },
    copy() {
      this.$message({
          message: 'OK',
          type: 'success'
      });
    }
  }
}
</script>

<style scoped>
.btn {
  width: 100%;
  margin-bottom: 10px;
}
</style>
