<template>
    <div class="e-int">
        <el-input-number v-model="num"></el-input-number>
    </div>
</template>

<script>
export default {
    data() {
        return {
            num: 1
        }
    },
    methods: {
        read(value) {
            this.num = value ? Math.round(value) : 0
        },
        write() {
            return this.num
        }
    }
}
</script>