<template>
    <div class="e-float">
        <el-input-number :precision="3" v-model="num"></el-input-number>
    </div>
</template>

<script>
export default {
    data() {
        return {
            num: 1
        }
    },
    methods: {
        read(value) {
            this.num = value || 0.0
        },
        write() {
            return this.num
        }
    }
}
</script>