<template>
    <div class="e-obejct">
        <!-- <el-collapse> -->
            <!-- <el-collapse-item :name="name"> -->
                <!-- <template slot="title">
                    Expand
                </template> -->
                <el-card shadow="never">
                     <!-- <div slot="header" class="clearfix">
                        <span>{{ name }}</span>
                        <el-button style="float: right; padding: 3px 0" type="text">Operation button</el-button>
                    </div> -->
                    <el-form
                        ref="form"
                        label-position="top"
                    >
                        <el-form-item 
                            v-for="[key,type] in entries"
                            :key="key"
                            :class=" type.component === 'Object' ? 'has-object' : 'no-object' "
                        >
                            <p class="form-label">{{ key }}:</p>
                            <component class="inner" :ref="'item-' + key" :is="type.component" v-bind="type.props || {}"></component>
                        </el-form-item>
                    </el-form>
                </el-card>
            <!-- </el-collapse-item> -->
        <!-- </el-collapse> -->
    </div>
</template>

<script>
export default {
    data() {
        return {
            entries: Object.entries(this.schema)
        }
    },
    props: ['name','schema'],
    methods: {
        read(value) {
            if(value == null) {
                value = {}
            }

            for (let [k,] of Object.entries(this.schema)) {
                const newValue = value[k] || null
                this.$refs['item-' + k][0].read(newValue)
            }
        },
        write() {
            let data = {}
            for (let [k,v] of Object.entries(this.schema)) {
                data[k] = this.$refs['item-'+k][0].write()
            }
            return data
        }
    }
}
</script>

<style>
.object-name {
    font-weight: bold;
    font-size: 150%;
    padding-left: 10px;
}
p.form-label {
    font-family: monospace;
    font-weight: bold;
    padding: 0%!important;
    margin: 0%!important;
}
.el-form-item.no-object > .el-form-item__content > * {
    display: inline;
}
.el-form-item.no-object .inner {
    padding-left: 20px;
}
.el-collapse-item__header, .el-collapse {
    border: none!important;
}
.el-form-item {
    margin-bottom: 10px!important;
}
.el-card__body {
    padding-bottom: 10px!important;
    padding-top: 10px!important;
}
</style>