<template>
    <div class="e-boolean">
        <el-switch v-model="val"></el-switch>
    </div>
</template>

<script>
export default {
    data() {
        return {
            val: false
        }
    },
    methods: {
        read(value) {
            this.val = !!value
        },
        write() {
            return this.val
        }
    }
}
</script>