<template>
    <div class="e-map">
        <div>
            <el-button size="mini" @click="add">Add</el-button>
             <!-- <el-button type="text">Clear</el-button> -->
        </div>

        <el-row :gutter="20" v-for="key in comps" :key="key">
            <el-col :span="2">
                <el-button type="danger" plain size="mini" @click="remove(key)">Delete</el-button>
            </el-col>
            <el-col :span="10">
                <component :ref="'key-' + key" :is="key_type.component" v-bind="key_type.props || {}"></component>
            </el-col>
            <el-col class="arrow" :span="2">
                <i class="el-icon-right"></i>
            </el-col>
            <el-col :span="10">
                <component :ref="'val-' + key" :is="val_type.component" v-bind="val_type.props || {}"></component>
            </el-col>
        </el-row>
    </div>
</template>

<script>
import Vue from "vue"
export default {
    data() {
        return {
            comps: []
        }
    },
    props: [
        'key_type',
        'val_type'
    ],
    methods: {
        read(value) {

           console.log("read: ", value)

           if(value == null) {
                this.comps = []
                return
            }

            for(let i = 1; i <= Object.keys(value).length; i++) {
                this.comps.push(i)
            }

            Vue.nextTick(() => {
                let i = 1;
                for(let [k,v] of Object.entries(value)) {
                    this.$refs['key-' + i][0].read(k)
                    this.$refs['val-' + i][0].read(v)
                    i++;
                }
            })
        },
        write() {
            let entries = this.comps.map(id => [
                this.$refs['key-'+id][0].write(),
                this.$refs['val-'+id][0].write()
            ])
            return Object.fromEntries(entries)
        },
        add() {
            this.comps.push(this.comps.length + 1)
        },
        remove(id) {
            this.comps = this.comps.filter(e => e != id)
        }
    }
}
</script>

<style>
.el-collapse-item__header:hover {
    background: gainsboro;
    transition: all 100ms;
}
</style>