<template>
    <div class="e-string">
        <el-input
            placeholder="..."
            clearable
            v-model="val"
        ></el-input>
    </div>
</template>

<script>
export default {
    data() {
        return {
            val: ""
        }
    },
    methods: {
        read(value) {
            this.val = value || ""
        },
        write() {
            return this.val
        }
    }
}
</script>

<style>
.el-input {
    max-width: 240px;
}
</style>