import {int, float, boolean, string, list, enumeration, object, map} from "./types"

const spawn_loc_type = enumeration('Spawn Type', ["Air","Ground","UnderWater","OnWater","TreeTop"])


const zone_meta = object('Zone Meta', {
    respawn: object('Respawn', {
        location: object('Location', {
            x: float(),
            y: float(),
            z: float()
        })
    }),
    spawnerConfig: object('Spawner Config', {
        pokemons: list(object('Pokemon', {
            type: spawn_loc_type,
            name: string(),
            fromLevel: int(),
            toLevel: int(),
            bossRate: float(),
            shinyRate: float(),
            drops:list(object('Drops', {
                id: string(),
                count: int(),
                rate: float()
            })),
            priority: int(),
            alolan: boolean(),
            spawnTime: enumeration('Spawn Time', ["DAY","EVENING","NIGHT","MORNING","ALL"])
        })),
        typeRates: map(spawn_loc_type, float())
    }),
    name: string(),
    showLeaveJoinLog: boolean(),
    denyWildPokemonBattle: boolean()
})


export const data = zone_meta