<template>
    <div class="e-list">
            <div>
                <el-button size="mini" @click="add">Add</el-button>
                <!-- <el-button type="text">Clear</el-button> -->
            </div>
            <el-collapse>
                <div v-for="item in comps" :key="item">
                    <el-collapse-item>
                        <template slot="title">
                            <el-tag type=dark>{{type.name}}</el-tag>
                            <el-button type="danger" plain size="mini" style="margin-left: 15px" @click="remove(item)">Delete</el-button>
                        </template>
                        <component :ref="'item-' + item" :is="type.component" v-bind="type.props || {}"></component>
                    </el-collapse-item>
                </div>
            </el-collapse>
    </div>
</template>

<script>
import Vue from "vue"
export default {
    data() {
        return {
            comps: []
        }
    },
    props: [
        'type'
    ],
    methods: {
        read(value) {
            this.comps = []

            if(value == null) {
                return
            }

            for(let i = 1; i <= value.length; i++) {
                this.comps.push(i)
            }
            Vue.nextTick(() => {
                for(let i = 1; i <= value.length; i++) {
                    this.$refs['item-' + i][0].read(value[i-1])
                }
            })
        },
        write() {
            return this.comps.map(id => this.$refs['item-'+id][0].write())
        },
        add() {
            this.comps.push(this.comps.length + 1)
        },
        remove(id) {
            this.comps = this.comps.filter(e => e != id)
        }
    }
}
</script>

<style>
.el-collapse-item__header:hover {
    background: gainsboro;
    transition: all 100ms;
}
</style>