<template>
    <div class="e-enumeration">
        <el-select v-model="val">
            <el-option
            v-for="item in values"
            :key="item"
            :label="item"
            :value="item">
            </el-option>
        </el-select>
    </div>
</template>

<script>
export default {
    data() {
        return {
            val: this.values[0]
        }
    },
    props: [
        'values'
    ],
    methods: {
        read(value) {
            this.val = value || this.values[0]
        },
        write() {
            return this.val
        },
    }
}
</script>