import Vue from "vue"
import Int from "./components/Int.vue"
import Float from "./components/Float.vue"
import Boolean from "./components/Boolean.vue"
import String from "./components/String.vue"
import List from "./components/List.vue"
import Enumeration from "./components/Enumeration.vue"
import Object from "./components/Object.vue"
import Map from "./components/Map.vue"

Vue.component('Int', Int)
Vue.component('Float', Float)
Vue.component('Boolean', Boolean)
Vue.component('String', String)
Vue.component('List', List)
Vue.component('Enumeration', Enumeration)
Vue.component('Object', Object)
Vue.component('Map', Map)

export function int() {
    return {
        component: 'Int',
        name: 'Int'
    }
}
export function float() {
    return {
        component: 'Float',
        name: 'Float'
    }
}
export function boolean() {
    return {
        component: 'Boolean',
        name: 'Boolean'
    }
}
export function string() {
    return {
        component: 'String',
        name: 'String'
    }
}
export function list(type) {
    return {
        component: 'List',
        name: `List(${ type.name })`,
        props: {
            type
        }
    }
}
export function enumeration(name, values) {
    return {
        component: 'Enumeration',
        props: {
            values
        },
        name
    }
}
export function object(name, schema) {
    return {
        component: 'Object',
        props: {
            name,
            schema
        },
        name
    }
}
export function map(key, value) {
    return {
        component: 'Map',
        name: `Map(${ key.name } => ${ value.name })`,
        props: {
            key_type: key,
            val_type: value
        }
    }
}